import { Hidden, MenuItem, Typography } from "@mui/material";
import * as React from "react";
import {
  CustomInputField,
  CustomTextFieldFilled,
} from "../../../Components/UI/CustomTextField/CustomTextField";
import classes from "./CreateNotification.module.scss";
import rightArrow from "../../../assets/icons/right_arrow.png";

import { useToastMessage } from "../../../contexts/ToastMessageContext";

import { createNotification } from "../../../api/systemSchedule";

interface ICreateNotificationProps {}

const CreateNotification: React.FunctionComponent<ICreateNotificationProps> = (
  props
) => {
  const { setErrorMessage, setSuccessMessage, setDisabled } = useToastMessage();

  const onSubmit = async (event) => {
    event.preventDefault();

    setDisabled();

    /*
    days.options.filter((option) => {
      if (option.active) {
        activeDays.push(option.id);
      }
    });
    */
    const data = {
      name: "title",
      days: ["Sunday", "Monday"],
      reminder_duration: 5,
      status: true,
      email_message: {
        active: true,
        message: "gfg",
        subject: "subject",
      },
      time: "2021-06-29T08:30:44.404+00:00",
      scheduled_for: "all",
      type: "lms",
    };

    const businessId = localStorage.getItem("businessId");

    const res = await createNotification(data, businessId);
    if (res.error) {
      return setErrorMessage(res.error);
    } else {
      return setSuccessMessage("Notification successfully created!");
    }
  };

  return (
    <div className={classes.container}>
      <div className="mt-3">
        <form onSubmit={onSubmit}>
          <Hidden xsDown implementation="js" only="xs">
            <h1 className={classes.formTitle}>New Lead Notification</h1>
          </Hidden>
          <div className="mt-3">
            <CustomInputField
              type="text"
              placeholder="Notification Title"
              rows={1}
              style={{ height: "60px" }}
            />
          </div>
          <div className="mt-3">
            <Typography variant="body1" gutterBottom component="h1">
              What time of the day?
            </Typography>
            <CustomInputField
              type="text"
              placeholder="Notification Time"
              rows={1}
              style={{ height: "60px" }}
            />
          </div>
          <div className="mt-3">
            <Typography variant="body1" gutterBottom component="h1">
              Which days?
            </Typography>
            <div
              style={{
                display: "flex",
                justifyContent: "space-evenly",
                marginBottom: "20px",
                marginTop: "20px",
                width: "100%",
              }}
            >
              <span className={classes.disabledDay}>Sun</span>
              <span className={classes.activeDay}>Mon</span>
              <span className={classes.activeDay}>Tue</span>
              <span className={classes.activeDay}>Wed</span>
              <span className={classes.activeDay}>Thu</span>
              <span className={classes.disabledDay}>Fri</span>
              <span className={classes.activeDay}>Sat</span>
            </div>
          </div>
          <div className="mt-3">
            <CustomTextFieldFilled
              id="filled-select-currency"
              select
              label="To Whom"
              variant="filled"
              type="select"
            >
              <MenuItem key={1} value="map">
                All
              </MenuItem>
            </CustomTextFieldFilled>
          </div>
          <div className="mt-3">
            <CustomTextFieldFilled
              id="filled-select-currency"
              select
              label="Time Range"
              variant="filled"
              type="select"
            >
              <MenuItem key={1} value="map">
                All
              </MenuItem>
            </CustomTextFieldFilled>
          </div>
          <div className="mt-3">
            <Typography variant="body1" gutterBottom component="h1">
              Email
            </Typography>
            <CustomInputField
              type="text"
              placeholder="Subject"
              rows={1}
              style={{ height: "60px" }}
            />
          </div>
          <div className="mt-3">
            <CustomInputField multiline placeholder="Message" rows={5} />
          </div>
          <div className={classes.buttonDiv}>
            <button className={classes.button} type="submit">
              Save Notification
              <img alt="save" src={rightArrow} style={{ margin: "5px" }} />
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default CreateNotification;
