/* eslint-disable no-useless-computed-key */
import * as React from "react";
import Brandlogo from "./Brandlogo/Brandlogo";
import classes from "./SettingSideBar.module.scss";
import { Divider, Button, List, ListItem, ListItemText } from "@mui/material/";
import { Collapse, withStyles } from "@material-ui/core";
//import { useBusinessProfile } from "../../contexts/BusinessProfileContext";
import { ExpandMore, ExpandLess } from "@mui/icons-material";
import { useBusinessProfile } from "../../contexts/BusinessProfileContext";

interface ISettingSideBarProps {}

const StyledListItem = withStyles({
  root: {
    backgroundColor: "white",
    borderRadius: "12px",
    width: "100%",
    height: "42px",
    "&:hover": {
      backgroundColor: "#f4f6f9",
    },
    ["@media (min-width:1600px)"]: {
      height: "70px",
    },
    fontSize: "1.5rem",
  },
})(ListItem);

const BusinessSettingSideBar: React.FunctionComponent<ISettingSideBarProps> = (
  props
) => {
  const [open, setOpen] = React.useState(false);

  const {
    getAllUserBusiness,
    fetchDefaultBusiness,
    selectedBusiness,
    allBusiness,
  } = useBusinessProfile();

  const handleClick = () => {
    setOpen(!open);
  };

  const loadDataRef = React.useRef(() => {
    !selectedBusiness && fetchDefaultBusiness();
    allBusiness.length < 1 && getAllUserBusiness();
  });

  React.useEffect(() => {
    loadDataRef.current();
  }, []);

  const exitSetting = () => {
    const redirectTo = selectedBusiness?.redirectUrl
      ? selectedBusiness.redirectUrl
      : "/engage/chat";

    window.location.href = redirectTo;
  };

  return (
    <div className={classes.Container}>
      <div className={classes.headContainer}>
        <Brandlogo />

        <h4 className={classes.pageTitle}>BUSINESS SETTINGS</h4>
      </div>
      {/* <div className={classes.selectBusiness}>
        <span className={classes.businessLabel}>SELECT BUSINESS</span>
        <FormControl fullWidth>
          <Select
            labelId="demo-simple-select-label"
            id="demo-simple-select"
            value="Honda North"
            label="Select Business"
            onChange={handleChange}
            
          >
            <MenuItem value={10}>Ten</MenuItem>
            <MenuItem value={20}>Twenty</MenuItem>
            <MenuItem value={30}>Thirty</MenuItem>
          </Select>
        </FormControl>
      </div> */}
      {/* list container */}
      <div style={{ padding: "10px" }}>
        <List>
          <a href="/business/general-settings" className={classes.anchor}>
            <StyledListItem
              selected={window.location.pathname.includes("general-settings")}
            >
              <ListItemText
                primary="General Settings"
                className={classes.listTextItem}
              />
            </StyledListItem>
          </a>
          <a href="/business/team-members" className={classes.anchor}>
            <StyledListItem
              selected={window.location.pathname.includes("team-members")}
            >
              <ListItemText
                primary="Team Members"
                className={classes.listTextItem}
              />
            </StyledListItem>
          </a>
          <a href="/business/lead-data-fields" className={classes.anchor}>
            <StyledListItem
              selected={window.location.pathname.includes("lead-data-fields")}
            >
              <ListItemText
                primary="Lead Data Fields"
                className={classes.listTextItem}
              />
            </StyledListItem>
          </a>
          {/*   <a
            href="/business/lead-distribution-rules"
            className={classes.anchor}
          >
            <StyledListItem
              selected={window.location.pathname.includes(
                "lead-distribution-rules"
              )}
            >
              <ListItemText
                primary="Lead Distribution Rules"
                className={classes.listText}
              />
            </StyledListItem>
          </a>
          <a href="/business/lead-notifications" className={classes.anchor}>
            <StyledListItem
              selected={window.location.pathname.includes("lead-notifications")}
            >
              <ListItemText
                primary="Lead Notifications"
                className={classes.listText}
              />
            </StyledListItem>
          </a> */}
          <StyledListItem
            onClick={handleClick}
            // selected={window.location.pathname.includes("whatsapp")}
          >
            <ListItemText primary="WhatsApp" className={classes.listTextItem} />
            {open ? <ExpandLess /> : <ExpandMore />}
          </StyledListItem>
          <Collapse in={open} timeout="auto" unmountOnExit>
            <List component="div" disablePadding>
              <a
                href="/business/wa-business-settings"
                className={classes.anchor}
              >
                <StyledListItem
                  sx={{ pl: 4 }}
                  selected={window.location.pathname.includes(
                    "wa-business-settings"
                  )}
                >
                  <ListItemText primary="Basic Settings" />
                </StyledListItem>
              </a>

              <a href="/business/whatsapp" className={classes.anchor}>
                <StyledListItem
                  sx={{ pl: 4 }}
                  selected={window.location.pathname.includes("whatsapp")}
                >
                  <ListItemText
                    primary="Usage and Reports"
                    // className={classes.listTextItem}
                  />
                </StyledListItem>
              </a>
              <a
                href="/business/whats-app/template-messages"
                className={classes.anchor}
              >
                <StyledListItem
                  sx={{ pl: 4 }}
                  selected={window.location.pathname.includes(
                    "template-messages"
                  )}
                >
                  <ListItemText primary="Template Messages" />
                </StyledListItem>
              </a>
            </List>
          </Collapse>
          {/* <a href="/Business/social-linking" className={classes.anchor}>
            <StyledListItem
              selected={window.location.pathname.includes("social-linking")}
            >
              <ListItemText
                primary="Social Media Linking"
                className={classes.listText}
              />
            </StyledListItem>
          </a> */}
          {/* <StyledListItem>
            <ListItemText primary="Billing" className={classes.listText} />
          </StyledListItem> */}
        </List>
      </div>
      {/* list container endsF */}
      {/* fixed bottom buttons */}
      <div className={classes.bottomButtons}>
        <span className={classes.guideText} onClick={() => exitSetting()}>
          Return to Evolve Next Workspace
        </span>
        <Divider variant="fullWidth" />
        <div
          style={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          {/* <Button className={classes.bottomButtom} style={{ color: "#24272C" }}>
            All Payments
          </Button> */}
          <Button
            className={classes.bottomButtom}
            onClick={() => exitSetting()}
          >
            Exit Settings{" "}
          </Button>
        </div>
      </div>
      {/* fixed bottom part ends */}
    </div>
  );
};

export default BusinessSettingSideBar;
