import { Divider } from "@mui/material";
import * as React from "react";
import LeadNotificationHeader from "../../../Components/BusinessSettings/LeadNotifications/LeadHead";
import LeadListItem from "../../../Components/BusinessSettings/LeadNotifications/LeadListItem";
import classes from "./LeadNotification.module.scss";
interface ILeadNotificationContainerProps {}

const LeadNotificationContainer: React.FunctionComponent<
  ILeadNotificationContainerProps
> = (props) => {
  return (
    <div className={classes.Team_Members}>
      <div className={classes.Header}>
        <LeadNotificationHeader />
      </div>
      <Divider />
      <div className={classes.Content}>
        <LeadListItem
          title="Morning Notification"
          description="10:00 AM | Sun  Mon  Tue  Wed  Thu  Fri  Sat All Team Members - Prev 17 Hrs. via Email"
        />
        <Divider />
        <LeadListItem
          title="Evening Notification"
          description="06:00 PM |  Sun  Mon  Tue  Wed  Thu  Fri  Sat All Team Members - Prev 8 Hrs. via Email"
        />
        <Divider />
      </div>
    </div>
  );
};

export default LeadNotificationContainer;
