import { IconButton } from "@material-ui/core";
import { Search, Clear } from "@material-ui/icons";
import { motion } from "framer-motion";
import clsx from "clsx";
import React, { useEffect, useRef, useState } from "react";
import classes from "./LeadHead.module.css";
import { MoreVert } from "@mui/icons-material";
import { Hidden } from "@mui/material";
import leftArrow from "../../../assets/icons/leftArrow.svg";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";

const LeadNotificationHeader = (props) => {
  const [isActive, setActive] = useState(false);

  const inputField = useRef(null);
  const variants = {
    open: { width: "100%", x: 0 },
    closed: { width: "100%", x: "80%" },
  };

  const SearchVariant = {
    open: {
      width: "85%",
      background: "#fff",
      x: 34,
      pointerEvents: "all",
      opacity: 1,
    },
    closed: {
      width: "100%",
      x: 0,
      background: "transparent",
      pointerEvents: "none",
      opacity: 0,
    },
  };

  const transition = { duration: 0.4, ease: [0.43, 0.13, 0.23, 0.96] };

  const handleSearch = (e) => {
    console.log(e.target.value);
  };

  useEffect(() => {}, [isActive]);
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <div
      className={clsx(
        classes.Container,
        "d-flex m-auto justify-content-between align-items-center"
      )}
      style={{ paddingTop: "10px" }}
    >
      <div
        style={{
          width: "70%",
        }}
        className="d-flex"
      >
        {/* Total enquiries */}
        {!isActive && (
          <div className={classes.LeadHead}>
            <Hidden smUp implementation="js">
              <img src={leftArrow} style={{ margin: "10px" }} alt="back" />
            </Hidden>
            <h2 className={classes.title}>Notifications</h2>
          </div>
        )}
        {/* Tab selector */}
      </div>
      {/* SearchIcon */}
      <motion.div
        animate={isActive ? "open" : "closed"}
        transition={transition}
        variants={SearchVariant}
        className={clsx(classes.Search, isActive && classes.Active)}
      >
        <motion.div
          initial={{ x: "80%" }}
          animate={isActive ? "open" : "closed"}
          variants={variants}
          transition={transition}
          className={clsx(classes.Icon_Container)}
        >
          {/* Search Icon */}
          <IconButton
            className={classes.Search_Icon}
            style={(isActive && { opacity: 0.5 }) || { opacity: 1 }}
            onClick={() => {
              setActive(true);
            }}
          >
            <Search />
          </IconButton>

          {/* Input field */}
          <motion.input
            onChange={handleSearch}
            ref={inputField}
            placeholder="Search People"
            transition={transition}
            animate={isActive ? "open" : "closed"}
            variants={{
              open: { opacity: 1, pointerEvents: "all" },
              closed: { opacity: 0, pointerEvents: "none" },
            }}
            initial={{ opacity: 0, width: "100%", pointerEvents: "none" }}
            className={classes.Input}
          />

          {/* Clear Icon */}

          <IconButton
            onClick={() => {
              return setActive(!isActive);
            }}
            className={classes.Clear}
          >
            <Clear />
          </IconButton>
        </motion.div>
      </motion.div>

      <div
        style={{ width: "70px" }}
        className="d-flex align-items-center justify-content-between"
      >
        <div className={classes.Search_Icon_Main}>
          {/* Search Icon */}
          <IconButton
            className={classes.Search_Icon}
            style={(isActive && { opacity: 0 }) || { opacity: 1 }}
            onClick={() => {
              return setActive((prevState) => !prevState);
            }}
          >
            <Search />
          </IconButton>
        </div>
        <div style={{ position: "relative" }} className={classes.More}>
          <IconButton onClick={handleClick}>
            <MoreVert />
          </IconButton>
          <Menu
            id="basic-menu"
            anchorEl={anchorEl}
            open={open}
            onClose={handleClose}
            keepMounted
            MenuListProps={{
              "aria-labelledby": "basic-button",
            }}
            anchorOrigin={{
              vertical: "bottom",
              horizontal: "center",
            }}
            transformOrigin={{
              vertical: "top",
              horizontal: "right",
            }}
          >
            <MenuItem onClick={handleClose}>Create New</MenuItem>
            <MenuItem onClick={handleClose}>Turn Off</MenuItem>
          </Menu>
        </div>
      </div>
    </div>
  );
};

export default LeadNotificationHeader;
