import * as React from "react";
import classes from "./LeadNotification.module.css";
import BusinessSettingSideBar from "../../../Container/BusinessSettings/BusinessSettingSideBar";
import { Hidden } from "@mui/material";
import LeadNotificationContainer from "../../../Container/BusinessSettings/LeadNotifications/LeadNotificationContainer";
import CreateNotification from "../../../Container/BusinessSettings/LeadNotifications/CreateNotification";
import MobileLeadNotification from "../../../Container/BusinessSettings/LeadNotifications/Mobile/MobileLeadNotification";
interface ILeadNotificationProps {}

const LeadNotification: React.FunctionComponent<ILeadNotificationProps> = (
  props
) => {
  return (
    <React.Fragment>
      <Hidden smUp implementation="js">
        <div className={classes.mobileMemberContainer}>
          <MobileLeadNotification />
        </div>
        <div>
          {/* <div className={classes.header}>
            <img
              src={require("../../../assets/icons/leftArrow.svg")}
              style={{ margin: "10px" }}
            />
            <h1 className={classes.headerTitle}>Invite a Team Member</h1>
          </div> */}
          <CreateNotification />
        </div>
      </Hidden>
      <Hidden xsDown implementation="js" only="xs">
        <div className={classes.Container}>
          <div className={classes.leftContainer}>
            <BusinessSettingSideBar />
          </div>
          <div className={classes.rightContainer}>
            <div className={classes.teamMemberDiv}>
              <LeadNotificationContainer />
            </div>
            <div className={classes.inviteForm}>
              <CreateNotification />
            </div>
          </div>
        </div>
      </Hidden>
    </React.Fragment>
  );
};

export default LeadNotification;
